import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../../components/header';
import './css/getOut.css'

class GetOut extends Component{
  render() {
    return(
      <div className="getOut">
        <Header content=""/>
        <Link to="/chooseCard" className="chooseCard">
          <div className="des-wrap">
            <div className="cover"></div>
            <div className="info">
              <div className="title"></div>
              <div className="type">8888</div>
            </div>
          </div>

          <div className="go"></div>
        </Link>

        <div className="box">
          <div className="title"></div>
          <div className="input-wrap">
            <span></span><input type="text"/>
          </div>
          <div className="des">
            <div className="info">888.88</div>
            <div className="getAll red"></div>
          </div>
        </div>

        <div className="getBtn getBtnAble"></div>
        <div className="getBtn getBtnDisable"></div>
      </div>
    )
  }
}

export default GetOut


