import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import TabBar from '../../components/tabBar'
import './home.css'


class Home extends Component {
  render() {
    return(
      <div className="home">
        <div className="entrance-wrap">
          <div className="title-info"></div>
          <div className="swiper"></div>

          <div className="topics">
            <Link to="/bless" className="topic">
              <img className="topic-icon" src={require('./images/entry1.png')} alt=""/>
              <span></span>
            </Link>
            <Link to="/systemMsg" className="topic">
              <img className="topic-icon" src={require('./images/entry2.png')} alt=""/>
              <span></span>
            </Link>
            <Link to="/noAccess" className="topic">
              <img className="topic-icon" src={require('./images/entry3.png')} alt=""/>
              <span></span>
            </Link>
            <Link to="/noAccess" className="topic">
              <img className="topic-icon" src={require('./images/entry4.png')} alt=""/>
              <span></span>
            </Link>
          </div>
        </div>

        <div className="my-money">
          <div className="my-money-item">
            <h4 className="money-name"></h4>
            <div className="money">
              <span className="m"></span>
              0.00
            </div>
            <Link to="/expend" className="money-detail"></Link>
          </div>
          <div className="my-money-item">
            <h4 className="money-name"></h4>
            <div className="money">
              <span className="m"></span>
              0.00
            </div>
            <Link to="/bill" className="money-detail"></Link>
          </div>
        </div>

        <Link to="/templateInfo" className="activity-wrap">
          <div className="activity">
            <img src={require('./images/add.png')} alt=""/>
            <div className="no-at"></div>
            <p className="no-at-info"></p>
          </div>
        </Link>

        <TabBar active="home"/>
      </div>
    )
  }
}

export default Home;


