import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../../components/header';
import './css/addCardStep.css';

class AddCardStep1 extends Component{
  render() {
    return(
      <div className="addCardStep1 addCardStep">
        <Header content=""/>

        <p className="title"></p>
        <div className="step-box">
          <div className="step-item">
            <span></span>
            <input type="text" placeholder=""/>
          </div>
          <div className="step-item">
            <span></span>
            <input type="text" placeholder=""/>
          </div>
        </div>

        <Link to="/addCardStep2" className="nextStep nextStepAble"></Link>
        <div className="nextStep nextStepDisable"></div>
      </div>
    )
  }
}

export default AddCardStep1;


